@import url("https://fonts.googleapis.com/css2?family=Barlow:wght@300;400;500;700&family=Fraunces:opsz,wght@9..144,400;9..144,500;9..144,700&display=swap");
.header-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2rem;
  background-color: hsl(43deg, 78%, 98%);
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 10;
}

.nav-list {
  display: flex;
  list-style: none;
  justify-content: center;
  flex-direction: column;
  gap: 2rem;
  position: fixed;
  background-image: linear-gradient(hsla(43deg, 78%, 98%, 0.5) hsl(43deg, 78%, 98%));
  left: -100%;
  top: 0;
  height: 100vh;
  width: 100%;
  font-size: 3rem;
  opacity: 0;
  transition: all 0.3s linear;
  text-align: left;
  margin-left: auto;
  font-family: "Fraunces", serif;
}
.nav-list li {
  padding: 0.5rem;
}
.nav-list a {
  letter-spacing: 0.0625rem;
  font-weight: 700;
  color: hsl(215deg, 19%, 25%);
}

.nav-list.active {
  z-index: 3;
  left: 0;
  opacity: 0.98;
}/*# sourceMappingURL=Nav.css.map */